import { connect } from 'react-redux';
import { withRouter } from 'react-router';

import Home from '../../components/panels/Home';
import { getSearchParams } from '../../selectors';
import actions from '../../actions';

const mapStateToProps = (state, { history }) => {
  const { city } = getSearchParams(state);
  return {
    isInitialized: state.app.isInitialized,
    withSubway: [2, 3].indexOf(city.SearchType) !== -1,
    selectCity: () => history.push('/cities'),
    selectSpeciality: () => history.push('/specialities'),
    selectStation: () => history.push('/stations')
  };
};

const mapDispatchToProps = (dispatch, { history }) => ({
  onSearch: ({ sort, date, city, speciality, station }) => {
    dispatch({
      type: actions.SEARCH,
      payload: {
        city: city.Id,
        speciality: speciality.Id,
        when: date,
        subwayStation: station && station.Id,
        page: 0,
        sort
      }
    });
    history.push('/search');
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Home)
);
